const app = getApp()
export default {
	data() {
		return {
			info:{},
			imgUrl: '',
			typeId: ''
		}
	},
	onLoad(option) {
		this.imgUrl =this.$helper.imgUrl
		this.id = option.id
		this.getInfo()
	},
	onShow() {
		this.typeId = uni.getStorageSync('coupon');
	},
	onUnload() {
		uni.removeStorageSync('coupon');
	},
	methods: {
		getInfo(){
			uni.showLoading({
				title:'加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}pay/calculationPrice`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					type: 'curricula',
					id: this.id
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						this.info = res.data;
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		gocourse(e){
			console.log('----------------')
			uni.navigateTo({
				url: `/pages/courseConvert?id=${e}`
			})
		},
		getSubmint(){
			console.log(this.typeId)
			if(this.typeId == ''){
				uni.showToast({
					title: '请选择兑换码',
					icon: 'none'
				})
				return false
			}
			uni.request({
				url: `${this.$helper.requestUrl}details/useExchangeCode`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					id: this.typeId
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '--------');
					if (res.status_code == 'ok') {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
						setTimeout(e =>{
							uni.redirectTo({
								url: '/pages/mePurchased'
							})
						},2000)
						// this.info = res.data;
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		}
		
	}
}
